import {useState} from 'react'
import Input from './Input'

function PopupWithForm(props) {
    const {name,title,isOpen,onClose,submitText,inputs,state} = props;

    const [fields,setFields] = useState({});
    const [errors,setErrors] = useState({});

    function handleChange(e,name) {
      setFields({... fields, [name]: e.target.value.trim()})
      setErrors({...errors, [name]: e.target.validationMessage})
    }

    function setButtonState() {
        const isField = Object.values(fields).filter(field => field && field).length;
        const isError =  Object.values(errors).filter(error => error && error).length;
        return !isField || isError;
    }
    
    return (
        <div 
          className={`popup popup_${name} 
          ${isOpen && 'visible'}`} 
          onClick={onClose}
          onSubmit={e => {
            e.preventDefault();

          }}
        >
            <form action="#" className="popup__form" name={name} noValidate>
                <button className="popup__close" aria-label="close-button" type="button"></button>
                <h3 className="popup__header">{title}</h3>
                {inputs && inputs.map(([type,placeholder,name,min,max],i) => (
                  <Input 
                    key={i} 
                    type={type} 
                    placeholder={placeholder} 
                    name={name}
                    min={min}
                    max={max}
                    value={fields[name] || ''}
                    error={errors[name] || ''}
                    handleChange={handleChange}
                  />
                ))}
                <button 
                  type="submit" 
                  className={`popup__submit ${setButtonState() && !state && 'inactive'}`}
                  aria-label="submit-button"
                  disabled={setButtonState() && !state}
                >
                    {submitText}
                </button>
            </form>
        </div>
    )
}

export default PopupWithForm